import clsx from "clsx";
import { ReactNode } from "react";
import React from "react";

export interface NavBarPropsContent {
  classNames?: {
    nav?: string;
    ul?: string;
  };
  children: ReactNode;
}

export function NavBarContent({ children, classNames }: NavBarPropsContent) {
  return (
    <nav className={clsx("container mx-auto flex py-4 px-6", classNames?.nav)}>
      <ul className={clsx("flex space-x-6", classNames?.ul)}>{children}</ul>
    </nav>
  );
}
